<template>
  <a-row class="center">
    <a-col :xs="{ span: 22}" :lg="{ span: 8}">
      <a-card title="用户登录" class="a-card">
        <a-form
          :form="form"
          :label-col="{span: 6, offset: 3}"
          :wrapper-col="{ span: 12}"
          class="form"
          @submit="handleSubmit"
        >
          <a-form-item label="用户名" >
            <a-input allow-clear v-decorator="['username', { rules: [{ required: true, message: '请输入用户名' }] }]" />
          </a-form-item>
          <a-form-item label="密码">
            <a-input-password
              allow-clear
              v-decorator="['password', { rules: [{ required: true, message: '请输入密码' }] }]"
            />
          </a-form-item>
          <a-form-item :wrapper-col="{ span: 12, offset: 9}">
            <a-button type="primary" html-type="submit">登录</a-button>
          </a-form-item>
        </a-form>
      </a-card>
    </a-col>
  </a-row>
</template>

<script>
import {examUser} from 'network/login'

export default {
  data() {
    return {
      form: this.$form.createForm(this)
    };
  },
  methods: {
    handleSubmit(e) {
      e.preventDefault();
      this.form.validateFields((err, values) => {
        if (!err) {
          examUser(values).then((res) => {
            localStorage.token  = res.token
            localStorage.username = res.username
            this.$message.success({ content: "登录成功", duration: 0.5 })
            this.$router.push('/index')
          })
        }
      });
    }
  }
};
</script>

<style scoped>
  .center{
    display: flex;
    height: 100vh;
    justify-content: center;
    align-items: center;
  }
  .a-card{
    box-shadow: 1px 1px 3px #000;
  }
</style>
